<template><div class="home">

<v-layout row justify-center class="mx-2 my-5">
	<v-flex xs12 sm10 md8 lg6 px-2>
		<div class="text-h4 text-center pa-5"> {{ 'Transliterations' | t }} </div>

		<p> {{ $t('When writing foreign words...') }} </p>

		<div class="d-flex">
		<v-select class="mb-4 flex" style="max-width: 400px"
			v-model="input_lang"
			:items="language_list"
			menu-props="auto"
			:label="$t('Select the language')"
			hide-details
			:prepend-icon="$mdi.Translate"
			single-line
			@change="change_page()"
		/>
		</div>

		 <div v-if="rules">
		 	<p> Here are the non-obvious correspondences between Nao and {{ language }}: </p>

		 	<div v-if="$vuetify.breakpoint.xs">
		 		<div class="my-3" v-for="row in rules">
		 			<div class="font-weight-bold">
		 				<span v-html="row[0].replace('<br>','')"> </span>
		 				<span class="px-2"> → </span>
		 				<span class="main_color" v-html="row[1].replace('<br>','')"> </span>
		 			</div>

		 			<div v-for="e in row[3]">
			      		<span v-html="e.orig"> </span> →
			      		<span class="NOWRAP">
			      			<span v-html="e.nao_html"> </span>
			      			<v-btn class="speaker_button" @click="$nao.pronounce($nao.disassemble(e.nao))" icon><v-icon small> {{ $mdi.VolumeHigh }} </v-icon></v-btn>
			      		</span>
			      	</div>
			      	<v-divider></v-divider>
		 		</div>
		 		
		 	</div>

			<v-simple-table v-else class="mb-4">
			<template v-slot:default>
			  <thead>
			    <tr>
			      <th> {{ language | t }} </th>
			      <th> Nao </th>
			      <th> {{ "Example" | t }} </th>
			    </tr>
			  </thead>
			  <tbody>
			    <tr v-for="row in rules">
			      <td v-html="row[0]">  </td>
			      <td v-html="row[1]">  </td>
			      <td>
			      	<span v-if="false" class="font-weight-medium" v-html="row[2]"> </span>
			      	<div v-for="e in row[3]">
			      		<span v-html="e.orig"> </span> →
			      		<span class="NOWRAP">
			      			<span v-html="e.nao_html"> </span>
			      			<v-btn class="speaker_button" @click="$nao.pronounce($nao.disassemble(e.nao))" icon><v-icon> {{ $mdi.VolumeHigh }} </v-icon></v-btn>
			      		</span>
			      	</div>
			      	
			      </td>
			    </tr>
			  </tbody>
			</template>
			</v-simple-table>
		</div>

		<div v-if="language == 'Mandarin'">
			<p class="text-h5 text-center"> Initials </p>
			<Awtable :arr="string_to_object( tr.Mandarin.initials )"/>
			<p class="text-h5 text-center"> Finals </p>
			<Awtable :arr="string_to_object( tr.Mandarin.finals )"/>
			<p class="text-h5 text-center"> Comparison with pinyin </p>
			<p> Here are the differences between Nao transliteration and pinyin (official transliteration for Mandarin Chinese): </p>
			<Awtable :arr="string_to_object( tr.Mandarin.pinyin )"/>
		</div>

		<div v-else-if="language == 'Cantonese'">
			<p class="text-h5 text-center"> Initials </p>
			<Awtable nohead :arr="string_to_object( tr.Cantonese.initials )"/>
			<p> * omitted if the final starts with the same vowel, e.g. 益 ik, 粵 iut, 碗 un </p>
			<p class="text-h5 text-center"> Finals </p>
			<Awtable nohead :arr="string_to_object( tr.Cantonese.finals )"/>
		</div>

		<div v-if="examples"> 
			<p> Here are some examples from {{ language }}: </p>

			<v-simple-table class="mb-4">
			<template v-slot:default>
			  <thead>
			    <tr>
			      <th> {{ language }} </th>
			      <th> Nao </th>
			      <th v-if="examples[0].length == 3"> English </th>
			    </tr>
			  </thead>
			  <tbody>
			    <tr v-for="row in examples">
			    	<td> {{ row[0] }} </td>
			    	<td> <nao pronounce :o="row[1]"/>  </td>
			    	<td v-if="row[2]"> {{ row[2] }} </td>
			    </tr>
			  </tbody>
			</template>
			</v-simple-table>
		</div>

		<div v-if="$nao.transliteration_languages.includes(language)">
			<p> Below you can transliterate any word from {{language}} to Nao: </p>
			<v-layout>
			    <v-text-field
			        dense filled disable
		        	rounded solo dense clearable
					:placeholder="translit_placeholder"
					@keydown.enter="set_word"
					v-model="translit_input"
					@click:clear="clear()"
					:append-icon="$mdi.Magnify"
					@click:append="set_word"
		        />
		    </v-layout>

		    <p class="text-center" v-if="word_to_translit.length > 0 && translit_result">
		    	{{word_to_translit}} → <nao pronounce :o="translit_result"/> 
		    </p>
		</div>

	</v-flex>
</v-layout>
</div></template>

<script>
import Awtable from '@/components/Awtable'
import nao from '@/components/Naoword'

// <v-btn @click="$nao.pronounce($nao.disassemble(row[2]))" icon><v-icon> mdi-volume-high </v-icon></v-btn>

function compare_objects(field) {
	return function (a,b) {
	    let fa = a[field].toLowerCase(),
	        fb = b[field].toLowerCase();

	    if (fa < fb) return -1;
	    if (fa > fb) return 1;
	    return 0;
	}
}

function string_to_object(s) {
	return s.trim().split(/\n+/).map(line => line.split(/\t+/) ) ;
}

let domain = "https://transliterations.herokuapp.com";

export default {
	components: {Awtable, nao},
	data() {
		return {
			input_lang: '',
			translit_input: '',
			word_to_translit: '',
			translit_result: null
		}
	},
	watch: {
		async word_to_translit() {
			this.translit_result = await this.transliterate_extended(this.language, this.word_to_translit);
		}
	},
	methods: {
		string_to_object,
		change_page() {
			this.$router.push('/transliterations/' + this.input_lang).catch(err => {});
			this.clear();
		},
		set_word() {
			this.word_to_translit = this.translit_input;
		},
		clear() {
			this.word_to_translit = '';
			this.translit_input = '';
		},
		async transliterate_extended(lang, w) {
			if (lang == 'Japanese') {
				let response = await fetch(domain + '/translit_japanese?text=' + w );
				w = (await response.json()).sentence;
				return this.$nao.transliterate_to_Nao('Japanese_hepburn', w);
			}
			else if (lang == 'Mandarin') {
				let response = await fetch(domain + '/translit_mandarin?text=' + w );
				w = (await response.json()).sentence;
				return this.$nao.transliterate_to_Nao('Chinese_pinyin', w);
			}
			else return this.$nao.transliterate_to_Nao(lang, w);
		}
	},
	computed: {
		rules() {
			function make_red(s) {
				return s.replace(/\[(.+?)\]/g, '<span class="marked">$1</span>');
			}

			if (!this.language || !this.tr[this.language].rules) return null;
			let res = string_to_object(this.tr[this.language].rules);

			for (let line of res) {
				if (line[2].trim().length == 0) continue;
				line.push(line[2].split('<br>').map(function(x) {
					//console.log(x);
					let f = x.match(/(.+)→\s+('.+)$/);
					if (!f) return {};
					return {orig: make_red(f[1]), nao: f[2].trim().replace(/\[|\]/g, ''), nao_html: make_red(f[2]) };
				}));
				line[2] = line[2].replace(/\[(.+?)\]/g, '<span class="marked">$1</span>');
			}
			return res;
		},
		examples() {
			if (!this.language || !this.tr[this.language].examples) return null;
			return string_to_object(this.tr[this.language].examples);
		},
		language() {
			return this.input_lang = this.$route.params.lang;
		},
		translit_placeholder() {
			return 'Type a word in ' + this.language;
		},
		/*
		translit_result() {
			this.$nao.transliterate_to_Nao(this.language, this.word_to_translit);
		},
		*/
		language_list() {
			return Object.keys(this.tr).map(item => ({value: item, text: this.$t(item) }) ).sort(compare_objects('text'));
		}
	},
	created() {
		this.tr = {
Belarusian: {
rules:`
Ж			j 		[Ж]лобін → '[j]lobin
х			h 		Бы[х]аў → 'by[h]au
г			g 		Салі[г]орск → 'sali[g]orsk
ц <br> (before hard vowels)		ts 		Слу[ц]к → 'slu[ts]k
ц <br> (before soft vowels)		t 		Ві[ц]ебск → 'vi[t]ebsk
дз <br> (before soft vowels)	d  		Жо[дз]іна → 'jo[d]ina <br> [Дз]мітрый → '[d]mitryi
ч			tc 		Рэ[ч]ыца → 're[tc]ytsa
е, э		е 		Алякс[е]й → 'aliaks[e]i <br> Андр[э]й → 'andr[e]i
ё 			io  	Магіл[ё]ў → 'magil[io]u 
ь			−		Гомел[ь] → 'gomel
ы 			y 		Маз[ы]р → 'maz[y]r
ю 			iu 		[Ю]рась → 'iuras
