щ		ct		Търгови[щ]е → 'tyrgovi[ct]e
ж		j		Наде[ж]да → 'nade[j]da
ц		ts		Вра[ц]а → 'vra[ts]a
ч		tc		Добри[ч] → 'dobri[tc]
